#navbar {
    width: 100%;
    display: inline;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 99;
    background: transparent;
}

#navbar ul {
    width: 100%;
}

#navbar li {
    display: inline;
    float: right;
    padding: 1%;
}

#navbar a {
    color: white;
    display: inline;
    text-decoration: none;
    font-size: 1em;
}

#navbar a:hover {
    color: white;
}

.sticky {
    position: fixed !important;
    background-color: #1f2833 !important;
}

